<template>
  <div class="empty_msg" v-if="hist.length == 0">
    {{ $t("history.empty_hist") }}
  </div>
  <div
    class="list"
    v-for="element in hist.slice().reverse()"
    :key="element.date_order"
  >
    <div class="element" @click="show_element(element.id)">
      <div class="heading">
        <div class="order">
          {{ $t("history.order") }} : {{ element.date_order }}
        </div>

        <div class="tag is-normal">
          {{ $t("history.total") }} : {{ this.calc_total(element) }} CHF
        </div>

        <div>
          <span class="material-icons-outlined"> arrow_drop_down </span>
        </div>
      </div>
      <div
        v-bind:class="'ordered_item ' + [element.id]"
        v-for="cart in element.lines"
        :key="cart"
      >
        <HistoryElement
          :name="cart.product_code"
          :quantity="cart.qty"
          :packag="cart.packaging"
          :price="cart.price_unit"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import HistoryElement from "@/components/history/HistoryElement";
import { getCartSale } from "@/types/Cart";
export default {
  name: "HistoryList",
  components: { HistoryElement },
  computed: mapState({
    hist: (state) => state.hist,
  }),
  async mounted() {
    await this.$store.dispatch("hist.fetchHist");
    getCartSale();
  },
  methods: {
    show_element(id) {
      var el = document.getElementsByClassName(id);
      for (var i = 0; i < el.length; i++) {
        el[i].classList.toggle("diplay_block");
      }
    },
    calc_total(element) {
      let total = 0;
      for (var el in element.lines) {
        total +=
          element.lines[el].qty * element.lines[el].price_unit.toFixed(2);
      }
      return total.toFixed(2);
    },
  },
};
</script>

<style scoped>
.list {
  width: 100%;
  padding-bottom: 5px;
  display: flex;
  flex-direction: row;
}
.heading {
  background-color: #d2ffc3;
  border: 2px solid #20ec65;
  padding: 5px;
  display: flex;
  flex-direction: row;
  height: 100%;
  align-items: center;
}
.element {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.ordered_item {
  display: none;
  padding: 5px;
}
.tag {
  background-color: #f3970c;
  border-color: #b4710c;
  color: black;
  margin-right: 10px;
  margin-left: auto;
  width: 150px;
}

.diplay_block {
  display: block;
}

.empty_msg {
  text-align: center;
}
</style>