<template>
  <app-layout>
    <template v-slot:header>
      <product-bar
        v-if="product != null"
        :productName="product.nameTranslations[$i18n.locale]"
      />
    </template>

    <template v-slot:content>
      <div v-if="info.image_url">
        <h1>{{ $t("produit.infogeneral") }}</h1>

        <div class="field is-grouped is-grouped-multiline">
          <div class="control">
            <div class="tags has-addons" v-html="ecoscore"></div>
          </div>

          <div class="control">
            <div
              class="tags has-addons"
              id="nutriscore"
              v-html="nutriscore"
            ></div>
          </div>

          <div class="control">
            <div class="tags has-addons">
              <span class="tag is-primary is-medium">{{
                $t("sidemenu.origin")
              }}</span>
              <span class="tag is-medium">{{ product.origin }}</span>
            </div>
          </div>

          <div class="control">
            <div class="tags has-addons">
              <span class="tag is-primary is-medium">{{
                $t("sidemenu.category")
              }}</span>
              <span class="tag is-medium">{{ categories }}</span>
            </div>
          </div>
        </div>

        <div class="img-wrapper">
          <Popper arrow>
            <template #content>
              <Modal
                :packagings="product.packagings"
                :id_item="product.productRef"
                :qt_item="0"
              />
            </template>
            <img :src="product.imageUrl" alt="image produit" />
          </Popper>
        </div>

        <h1 class="title">{{ $t("produit.seasonalite") }}</h1>
        <table class="table">
          <thead>
            <tr class="columns bg" v-if="!mq.phone" v-html="arrow"></tr>
            <tr class="columns has-background-white" v-html="val"></tr>
          </thead>
        </table>

        <h1 class="title">{{ $t("produit.nutritionel") }}</h1>

        <Bar
          :chart-options="chartOptions"
          :chart-data="chartData"
          :chart-id="chartId"
          :dataset-id-key="datasetIdKey"
          :plugins="plugins"
          :css-classes="cssClasses"
          :styles="styles"
          :width="width"
          :height="height"
        />
      </div>
    </template>
  </app-layout>
</template>

<script>
import { mapState } from "vuex";
import axios from "axios";
import AppLayout from "../layout/AppLayout.vue";
import ProductBar from "./ProductBar.vue";
import { getProductFromRef } from "@/types/Product";
import { Bar } from "vue-chartjs";
import Modal from "@/components/Modal.vue";
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  BarElement,
  CategoryScale,
  LinearScale,
} from "chart.js";

import Popper from "vue3-popper";

ChartJS.register(
  Title,
  Tooltip,
  Legend,
  BarElement,
  CategoryScale,
  LinearScale
);

export default {
  inject: ["mq"],
  name: "Product",
  computed: mapState({
    db: (state) => state.db,
  }),
  components: { AppLayout, ProductBar, Bar, Modal, Popper },
  props: {
    chartId: {
      type: String,
      default: "bar-chart",
    },
    datasetIdKey: {
      type: String,
      default: "label",
    },
    width: {
      type: Number,
      default: 20,
    },
    height: {
      type: Number,
      default: 20,
    },
    cssClasses: {
      default: "",
      type: String,
    },
    styles: {
      type: Object,
      default: () => {},
    },
    plugins: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      product: null,
      seasonal: new Date().getMonth() + 1,
      val: "",
      ecoscore: "",
      nutriscore: "",
      arrow: "",
      info: {},
      chartData: {
        labels: [],
        datasets: [
          {
            data: [],
            backgroundColor: [
              "rgba(255, 99, 132, 0.2)",
              "rgba(54, 162, 235, 0.2)",
              "rgba(255, 206, 86, 0.2)",
              "rgba(75, 192, 192, 0.2)",
              "rgba(153, 102, 255, 0.2)",
              "rgba(255, 159, 64, 0.2)",
            ],
          },
        ],
      },
      chartOptions: {
        responsive: true,
        indexAxis: "y",
        plugins: {
          legend: {
            display: false,
          },
          title: {
            display: true,
            text: this.$i18n.t("produit.infogeneral100"),
          },
        },
      },
      categories: "",
    };
  },
  async mounted() {
    this.product = await getProductFromRef(this.$route.params.ref, this.db);
    this.product.category.split("/").forEach((category) => {
      if (category != "All ") {
        this.categories += category + ",";
      }
    });
    this.categories = this.categories.slice(0, -1);
    this.val = this.show_seasonal(this.product);

    axios
      .get(
        "https://world.openfoodfacts.org/api/v0/product/" +
          this.$route.params.barcode +
          ".json"
      )
      .then((response) => {
        this.info = response.data.product;
        this.nutriscore = this.show_nutriscore();
        this.ecoscore = this.show_ecoscore();
        this.prepareChart();
      });
  },
  methods: {
    show_seasonal(product) {
      const month = [
        this.$i18n.t("produit.janvier"),
        this.$i18n.t("produit.fevrier"),
        this.$i18n.t("produit.mars"),
        this.$i18n.t("produit.avril"),
        this.$i18n.t("produit.mai"),
        this.$i18n.t("produit.juin"),
        this.$i18n.t("produit.juillet"),
        this.$i18n.t("produit.aout"),
        this.$i18n.t("produit.septembre"),
        this.$i18n.t("produit.octobre"),
        this.$i18n.t("produit.novembre"),
        this.$i18n.t("produit.decembre"),
      ];
      let tab = "";
      const current_month = new Date().getMonth();
      for (let i = 0; i < month.length; i++) {
        if (current_month == i) {
          this.arrow += `<th class="column"><figure class="image is-32x32"><img src="./images/fleche.png" alt="image"/></figure></th>`;
        } else {
          this.arrow += `<th class="column"></th>`;
        }
        if (
          (product.seasonStart >= i && product.seasonEnd <= i) ||
          (product.seasonStart <= i && product.seasonEnd >= i)
        ) {
          tab +=
            `<th class="px-0 has-text-centered column"><abbr>` +
            month[i] +
            `</abbr></th>`;
        } else {
          tab +=
            `<th class="is-selected px-0 has-text-centered column"><abbr>` +
            month[i] +
            `</abbr></th>`;
        }
      }
      return tab;
    },
    show_nutriscore() {
      switch (this.info.nutriscore_grade) {
        case "a":
          return "<span class='tag is-primary is-medium' >Nutriscore</span> <span class='tag is-success is-medium'>A</span>";
        case "b":
          return "<span class='tag is-primary is-medium' >Nutriscore</span><span class='tag is-primary is-medium'>B</span>";
        case "c":
          return "<span class='tag is-primary is-medium' >Nutriscore</span><span class='tag is-warning is-medium'>C</span>";
        case "d":
          return "<span class='tag is-primary is-medium' >Nutriscore</span><span class='tag is-warning is-medium'>D</span>";
        case "e":
          return "<span class='tag is-primary is-medium' >Nutriscore</span><span class='tag is-danger is-medium'>E</span>";
        default:
          return (
            "<span class='tag is-primary is-medium' >Nutriscore</span><span class='tag is-medium'>" +
            this.$i18n.t("produit.no_info") +
            "</span>"
          );
      }
    },
    show_ecoscore() {
      switch (this.info.ecoscore_grade) {
        case "a":
          return "<span class='tag is-primary is-medium' >Ecoscore</span> <span class='tag is-success is-medium'>A</span>";
        case "b":
          return "<span class='tag is-primary is-medium' >Ecoscore</span><span class='tag is-primary is-medium'>B</span>";
        case "c":
          return "<span class='tag is-primary is-medium' >Ecoscore</span><span class='tag is-warning is-medium'>C</span>";
        case "d":
          return "<span class='tag is-primary is-medium' >Ecoscore</span><span class='tag is-warning is-medium'>D</span>";
        case "e":
          return "<span class='tag is-primary is-medium' >Ecoscore</span><span class='tag is-danger is-medium'>E</span>";
        default:
          return (
            "<span class='tag is-primary is-medium' >Ecoscore</span><span class='tag is-medium'>" +
            this.$i18n.t("produit.no_info") +
            "</span>"
          );
      }
    },
    prepareChart() {
      for (const [key, value] of Object.entries(this.info.nutriments)) {
        if ((key.includes("100g") && value != null) && (value > 1) && (key.length< 25) && (this.info.nutriments[key.replace('_100g',"_unit")] != undefined)) {
          this.chartData.labels.push(key.replace("_100g", "")+" ("+this.info.nutriments[key.replace('_100g',"_unit")]+")");
          this.chartData.datasets[0].data.push(value);
        }
      }
    },
  },
};
</script>

<style scoped>
.img-wrapper {
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
}

.image {
  width: 100px;
  height: 50px;
  object-fit: cover;
}
.bg {
  background-color: #efede6;
}

#imageProd {
  margin: 0 auto !important;
}
</style>