
import { mapState } from "vuex";
import Modal from "./Modal.vue";
import { Product, productIsOfCurrentSeason } from "../types/Product";
import { changeFavorite, isFavorite } from "../types/Favorite";
import { defineComponent, PropType } from "@vue/runtime-core";
// @ts-ignore
import Popper from "vue3-popper";

export default defineComponent({
  inject: ["mq"],
  name: "CardImage",
  components: {
    Modal,
    Popper,
  },
  props: {
    product: {
      type: Object as PropType<Product>,
      required: true,
    },
  },
  mounted() {
    this.fav = isFavorite(this.product.productRef);
  },
  data() {
    return new (class {
      interval: ReturnType<typeof setInterval> | null = null;
      count: number = 0;
      fav: boolean = false;
    })();
  },

  computed: mapState({
    user: (state: any) => state.user,
  }),

  methods: {
    start() {
      if (!this.interval) {
        this.interval = setInterval(() => this.count++, 30);
      }
    },

    stop() {
      if (this.count > 15) {
        this.$router.push(
          "/product/" + this.product.barcode + "/" + this.product.productRef
        );
      }
      if (this.interval) {
        clearInterval(this.interval);
        this.interval = null;
      }
      this.count = 0;
    },

    changeFavorite(product: Product, e: Event) {
      e.stopPropagation();
      changeFavorite(product);
      this.fav = isFavorite(this.product.productRef);
    },
    getUnitPrice(product: Product) {
      if (product.packagings.length > 0) {
        return product.packagings[0].unitPrice.toFixed(2);
      }
      return "0.00";
    },
    isSeasonal(product: Product) {
      return productIsOfCurrentSeason(product);
    },
  },
});
