<template>
<div class="elementCart">

  <div class="name"  v-if="product != null">
    {{ ($i18n.locale == "en")?product.name:product.nameTranslations[$i18n.locale] }}
  </div>

  <div class="pack">
      {{ packag }} {{ price.toFixed(2) }} CHF x {{ quantity }}
  </div>

  <div class="price">
  {{ (price.toFixed(2) * quantity).toFixed(2) }} CHF
  </div>

</div>
</template>


<script>
import { mapState } from "vuex"
import {getProductFromRef} from "@/types/Product"
export default {
  name: "CartElement",  
  computed: mapState({
    db: (state) => state.db,
  }),

  props: {
    name: {
      type: String,
      required: true,
    },
    packag: {
      type: String,
      required: true,
    },
    quantity: {
      type: String,
      required: true,
    },
    price: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      product : null
    }
  },
  async mounted(){
    this.product = await getProductFromRef(this.name,this.db)
  }
};
</script>


<style scoped>

.elementCart{
  width: 100%;
  display: flex;
  flex-direction: row;
  background-color: white;
}
.name{
  justify-items: left;
  margin-left: 5px;
  padding-right:5px;
}
.price{
  margin-left: auto;
  margin-right: 5px;
  justify-items: right;
}
.pack{
  justify-items: center;
}


</style>
